import React from "react";
import axios from "axios";

export default class UserSearch extends React.Component {
  state = {
    query: ""
  };

  userInfo = () => {
    return (
      <div style={{ backgroundColor: "#f8f8f8" }}>
        <p>ùôêùô®ùôöùôß: {this.state.result.data.username}</p>
        <p>ùôãùô§ùôûùô£ùô©ùô®: {this.state.result.data.points}</p>
        <p>
          ùòæùôùùôñùô£ùôòùôö ùô§ùôõ ùôíùôûùô£ùô£ùôûùô£ùôú ùôÇùôûùô´ùôöùôñùô¨ùôñùôÆ:{" "}
          {Math.round(this.state.result.data.chance * 10000) / 100}%
        </p>
      </div>
    );
  };

  handleInputChange = async () => {
    await this.setState({
      query: this.search.value
    });
  };

  handleSubmit = async () => {
    const params = { username: this.state.query };
    try {
      const response = await axios.get(
        "http://secure-anchorage-54009.herokuapp.com/api/search",
        {
          params
        }
      );
      await this.setState({ result: response });
    } catch (e) {
      return;
    }
  };

  render() {
    return (
      <div>
        <h3>üîé ùôéùôöùôñùôßùôòùôù ùôõùô§ùôß ùôÆùô§ùô™ùôß ùô™ùô®ùôöùôßùô£ùôñùô¢ùôö:</h3>
        <input
          ref={input => (this.search = input)}
          onChange={this.handleInputChange}
        />
        <input type="submit" onClick={this.handleSubmit} />
        {this.state.result ? <this.userInfo /> : <div />}
      </div>
    );
  }
}
